.toDoList .taskName.row {
    text-align: start;
}

.toDoList .task {
    position: relative;
    padding: 5px;
    margin-top: 5px;
    text-align: left;
    width: 400px;
    margin: 0 auto;
}

.toDoList .task .delete {
    float: right;
    color: red;
    cursor: pointer;
    display: none;
    position: absolute;
    right: 3px;
    top: 5px;
}

.toDoList .tasks {
    padding: 10px;
}

.toDoList .task:hover {
    background-color: #5bc0de
}

.toDoList .task:hover .delete {
    display: inline-block;
}

.toDoList .task.done {
    text-decoration: line-through;
}

.toDoList .header {
    display: grid;
    position: relative;
}

.toDoList .header .img-creat {
    width: 70px;
    top: -20px;
    left: 350px;
    display: none;
}

.toDoList .header .img-creat-blok {
    width: 70px;
    top: -20px;
    left: 560px;
    position: absolute;
    display: inline-block;
}

.toDoList .tasks div .img-task {
    width: 50px;
    position: absolute;
    top: -10px;
    left: -50px;
    display: none;
}

.toDoList .tasks div .img-task-blok {
    width: 50px;
    position: absolute;
    top: -10px;
    left: -50px;
    display: block;
}
